.dev-share-buttons {
  -ms-flex-align: center;
      align-items: center;
  background: rgba(0, 0, 0, 0.8);
  bottom: 0;
  display: none;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 150; }

.dev-share-buttons.show {
  display: -ms-flexbox;
  display: flex; }

.dev-share-buttons__item {
  display: -ms-flexbox !important;
  display: flex !important;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center; }

.dev-share-buttons iframe {
  border: 0;
  bottom: 0;
  width: 0;
  height: 0;
  position: absolute;
  left: 0;
  right: 0;
  top: 0; }

.dev-share-buttons.show-iframe iframe {
  height: 100%;
  width: 100%; }

.dev-share-buttons a span.hide_text:not(.dev-share-buttons__text--copy) {
  width: 0; }

.dev-share-buttons .close {
  font-size: 32px;
  position: absolute;
  top: 0.3em;
  right: 0.3em; }

.dev-share-buttons .close:hover {
  cursor: pointer; }

/* Hide the text. */
.hide_text {
  display: block;
  overflow: hidden;
  text-indent: 100%;
  white-space: nowrap; }
